    // --- Schedule Manipulation Handlers ---

    const handleDuplicateDay = (sourceDate: Date) => {
        const targetDate = addDays(sourceDate, 1);
        const sourceDateKey = format(sourceDate, 'yyyy-MM-dd');
        const targetDateKey = format(targetDate, 'yyyy-MM-dd');

        if (!scheduleData[sourceDateKey]) {
            toast({ title: "Nada que Duplicar", description: `No hay horario definido para el ${format(sourceDate, 'PPP', { locale: es })}.`, variant: "default" });
            return;
        }

        // Deep copy assignments, generating new IDs for each assignment
        const copiedAssignments: { [deptId: string]: ShiftAssignment[] } = {};
        const sourceAssignments = scheduleData[sourceDateKey].assignments || {};
        for (const deptId in sourceAssignments) {
            copiedAssignments[deptId] = sourceAssignments[deptId].map(assign => ({
                ...assign,
                id: `shift_${uuidv4()}` // Generate a new unique ID
            }));
        }

        setScheduleData(prev => ({
            ...prev,
            [targetDateKey]: {
                date: targetDate,
                assignments: copiedAssignments
            }
        }));
        toast({ title: "Horario Duplicado", description: `El horario del ${format(sourceDate, 'dd/MM')} se copi al ${format(targetDate, 'dd/MM/yyyy')}.` });
         // Optionally, move the view to the next day
         setCurrentDate(targetDate);
         if(viewMode === 'week') {
             // If duplicating the last day of the week view, move to the next week
            const weekEnd = endOfWeek(sourceDate, { weekStartsOn: 1 });
             if (isSameDay(sourceDate, weekEnd)) {
                 handleNext(); // Navigate to the next week
             }
         }
    };

    // Handler to duplicate the entire week's schedule to the next week
     const handleDuplicateWeek = () => {
         const nextWeekStart = addWeeks(startOfWeek(currentDate, { weekStartsOn: 1 }), 1);
         let assignmentsCopiedCount = 0;

         setScheduleData(prev => {
             const newSchedule = { ...prev };
             weekDates.forEach((sourceDate, index) => {
                 const sourceDateKey = format(sourceDate, 'yyyy-MM-dd');
                 const targetDate = addDays(nextWeekStart, index);
                 const targetDateKey = format(targetDate, 'yyyy-MM-dd');

                 if (prev[sourceDateKey]?.assignments && Object.keys(prev[sourceDateKey].assignments).length > 0) {
                     // Deep copy with new IDs
                     const copiedAssignments: { [deptId: string]: ShiftAssignment[] } = {};
                     const sourceAssignments = prev[sourceDateKey].assignments;
                     for (const deptId in sourceAssignments) {
                         copiedAssignments[deptId] = sourceAssignments[deptId].map(assign => ({
                             ...assign,
                             id: `shift_${uuidv4()}`
                         }));
                          assignmentsCopiedCount += copiedAssignments[deptId].length;
                     }
                     newSchedule[targetDateKey] = {
                         date: targetDate,
                         assignments: copiedAssignments
                     };
                 } else {
                      // If source day is empty, ensure target day is also empty or removed
                      if (newSchedule[targetDateKey]) {
                         // Optionally clear it: delete newSchedule[targetDateKey];
                         // Or set empty assignments: newSchedule[targetDateKey] = { date: targetDate, assignments: {} };
                      }
                 }
             });
             return newSchedule;
         });

         if (assignmentsCopiedCount > 0) {
             toast({ title: "Semana Duplicada", description: `Se copiaron ${assignmentsCopiedCount} turnos a la semana del ${format(nextWeekStart, 'dd/MM')}.` });
             setCurrentDate(nextWeekStart); // Navigate to the duplicated week
         } else {
             toast({ title: "Semana Vaca", description: "La semana actual no tena turnos para duplicar.", variant: "default" });
         }
     };


    const handleClearDay = (dateToClear: Date) => {
        const dateKey = format(dateToClear, 'yyyy-MM-dd');
         let shiftsCleared = 0;
         let notesCleared = 0;

        setScheduleData(prev => {
            if (!prev[dateKey]) return prev; // No data to clear
             const daySchedule = prev[dateKey];
             shiftsCleared = Object.values(daySchedule.assignments || {}).reduce((sum, deptAssigns) => sum + deptAssigns.length, 0);

            const newSchedule = { ...prev };
            // Create a new object for the day with empty assignments
            newSchedule[dateKey] = { date: dateToClear, assignments: {} };
            return newSchedule;
        });

         // Also clear notes for that day
         setScheduleNotes(prevNotes => {
             const notesBefore = prevNotes.length;
             const updatedNotes = prevNotes.filter(note => note.date !== dateKey);
             notesCleared = notesBefore - updatedNotes.length;
             return updatedNotes;
         });


         if (shiftsCleared > 0 || notesCleared > 0) {
            toast({ title: "Da Limpiado", description: `Se eliminaron ${shiftsCleared} turnos y ${notesCleared} anotaciones del ${format(dateToClear, 'PPP', { locale: es })}.`, variant: "destructive" });
         } else {
             toast({ title: "Da Vaco", description: `No haba turnos ni anotaciones para eliminar el ${format(dateToClear, 'PPP', { locale: es })}.`, variant: "default" });
         }
    };

     // Handler to clear the entire week's schedule
     const handleClearWeek = () => {
         let shiftsCleared = 0;
         let notesCleared = 0;
         const weekDateKeys = weekDates.map(date => format(date, 'yyyy-MM-dd'));

         setScheduleData(prev => {
             const newSchedule = { ...prev };
             let changed = false;
             weekDateKeys.forEach(dateKey => {
                 if (newSchedule[dateKey]?.assignments && Object.keys(newSchedule[dateKey].assignments).length > 0) {
                     shiftsCleared += Object.values(newSchedule[dateKey].assignments).reduce((sum, deptAssigns) => sum + deptAssigns.length, 0);
                     newSchedule[dateKey] = { date: parseDateFns(dateKey, 'yyyy-MM-dd', new Date()), assignments: {} }; // Keep date object, clear assignments
                     changed = true;
                 }
             });
             return changed ? newSchedule : prev;
         });

          // Also clear notes for the week
          setScheduleNotes(prevNotes => {
              const notesBefore = prevNotes.length;
              const updatedNotes = prevNotes.filter(note => !weekDateKeys.includes(note.date));
              notesCleared = notesBefore - updatedNotes.length;
              return updatedNotes.length !== notesBefore ? updatedNotes : prevNotes;
          });


         if (shiftsCleared > 0 || notesCleared > 0) {
             toast({ title: "Semana Limpiada", description: `Se eliminaron ${shiftsCleared} turnos y ${notesCleared} anotaciones de la semana actual.`, variant: "destructive" });
         } else {
             toast({ title: "Semana Vaca", description: "No haba turnos ni anotaciones para eliminar en esta semana.", variant: "default" });
         }
     };


